import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { FC } from "react";
import { useCurrentDateEffect } from "../Time/Time";
import "./Reminder.scss";

interface ITimeUtility {
  format: (date: Date) => string;
  formatHours: (hours: number) => string;
  formatSegment: (segment: number) => string;
  findAMorPM: (hours: number) => string;
}


const T: ITimeUtility = {
  format: (date: Date): string => {
    const hours: string = T.formatHours(date.getHours()),
      minutes: number = date.getMinutes(),
      seconds: number = date.getSeconds();

    return `${hours}:${T.formatSegment(minutes)} ${T.findAMorPM(
      date.getHours()
    )}`;
  },
  formatHours: (hours: number): string => {
    return (hours % 12 === 0 ? 12 : hours % 12).toString();
  },
  formatSegment: (segment: number): string => {
    return (segment < 10 ? `0${segment}` : segment).toString();
  },
  findAMorPM: (hours: number): string => {
    return hours < 12 ? "AM" : "PM";
  },
};

interface ReminderProps {}

const Reminder: FC<ReminderProps> = () => {
  const date: Date = useCurrentDateEffect();


  return (
    <div className="reminder">
      <div className="reminder-icon">
        {/* <i className="bullhorn" /> */}
        <FontAwesomeIcon
          icon="bullhorn"
          color="rgb(245, 245, 245)"
        ></FontAwesomeIcon>
        <span className="badge">3</span>
      </div>
      <span className="reminder-text">
        <b >{T.format(date)}</b> :: You there !! Let's
        Debug..{}
      </span>
      {/* <span className="reminder-time">{T.format(date)}</span> */}
    </div>
  );
};

export default Reminder;
